import React, { Component } from 'react';
import { View, Text, TouchableOpacity } from 'react-native';
import { connect } from 'react-redux';
import { SPopup, STheme } from 'servisofts-component';
import Usuario from '../../..';
class CerrarSession extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    render() {
        return (
            <TouchableOpacity style={{
                margin: 4,
                width: 100,
                height: 40,
                borderColor: "#ddd",
                borderRadius: 4,
                backgroundColor: "#99000099",
                justifyContent: "center",
                alignItems: "center"
            }} onPress={() => {
                SPopup.confirm({
                    title: "Cerrar sesin", message: "Seguro que desea desconectar su usuario?", onPress: () => {
                        Usuario.Actions.logout(this.props)
                    }
                })
            }}>
                <Text style={{
                    fontSize: 10,
                    color: STheme.color.text,
                    fontWeight: "bold",
                    textAlign: "center"
                }}> Cerrar sesin  </Text>
            </TouchableOpacity>
        );
    }
}
const initStates = (state) => {
    return { state }
};
export default connect(initStates)(CerrarSession);